<template>
  <div class="info">
    <p class="title">编辑个人资料</p>
    <hr />
    <div class="personal">
      <el-form :model="registerForm" class="demo-ruleForm" label-width="80px">
        <el-form-item prop="username" label="userName">
          <el-input
            v-model="registerForm.username"
            placeholder="userName"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password" label="password">
          <el-input
            type="password"
            placeholder="password"
            v-model="registerForm.password"
          ></el-input>
        </el-form-item>
        <el-form-item label="gender">
          <el-radio-group v-model="registerForm.sex">
            <el-radio :label="0">女</el-radio>
            <el-radio :label="1">男</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item prop="phoneNum" label="phone">
          <el-input
            placeholder="phone"
            v-model="registerForm.phoneNum"
          ></el-input>
        </el-form-item>
        <el-form-item prop="email" label="email">
          <el-input v-model="registerForm.email" placeholder="email"></el-input>
        </el-form-item>
        <el-form-item prop="birth" label="birthday">
          <el-date-picker
            type="date"
            placeholder="Please select a date"
            v-model="registerForm.birth"
            style="width: 100%;"
          ></el-date-picker>
        </el-form-item>
        <el-form-item prop="introduction" label="signature">
          <el-input
            type="textarea"
            placeholder="signature"
            v-model="registerForm.introduction"
          ></el-input>
        </el-form-item>
        <el-form-item prop="location" label="region">
          <el-select
            v-model="registerForm.location"
            placeholder="region"
            style="width:100%"
          >
            <el-option
              v-for="item in cities"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div class="btn">
        <div @click="saveMsg()">Save</div>
        <div @click="goback">Cancel</div>
      </div>
    </div>
  </div>
</template>

<script>
import mixin from "../mixins";
import { mapGetters } from "vuex";
import { cities } from "../assets/data/form";
import { HttpManager } from "../api/index";

export default {
  name: "info",
  mixins: [mixin],
  data: function() {
    return {
      registerForm: {
        username: "",
        password: "",
        sex: "",
        phoneNum: "",
        email: "",
        birth: "",
        introduction: "",
        location: ""
      },
      cities: cities
    };
  },
  computed: {
    ...mapGetters(["userId"])
  },
  mounted() {
    this.getMsg(this.userId);
  },
  methods: {
    getMsg(id) {
      HttpManager.getUserOfId(id)
        .then(res => {
          this.registerForm.username = res[0].username;
          this.registerForm.password = res[0].password;
          this.registerForm.sex = res[0].sex;
          this.registerForm.phoneNum = res[0].phoneNum;
          this.registerForm.email = res[0].email;
          this.registerForm.birth = res[0].birth;
          this.registerForm.introduction = res[0].introduction;
          this.registerForm.location = res[0].location;
          this.registerForm.avator = res[0].avator;
        })
        .catch(err => {
          console.log(err);
        });
    },
    goback() {
      this.$router.go(-1);
    },
    saveMsg() {
      let d = new Date(this.registerForm.birth);
      let datetime =
        d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate();
      let params = new URLSearchParams();
      params.append("id", this.userId);
      params.append("username", this.registerForm.username);
      params.append("password", this.registerForm.password);
      params.append("sex", this.registerForm.sex);
      params.append("phone_num", this.registerForm.phoneNum);
      params.append("email", this.registerForm.email);
      params.append("birth", datetime);
      params.append("introduction", this.registerForm.introduction);
      params.append("location", this.registerForm.location);
      HttpManager.updateUserMsg(params)
        .then(res => {
          if (res.code === 1) {
            this.showError = false;
            this.showSuccess = true;
            this.$store.commit("setUsername", this.registerForm.username);
            this.$notify.success({
              title: "Edited successfully",
              showClose: true
            });
            setTimeout(function() {
              this.$router.go(-1);
            }, 2000);
          } else {
            this.showSuccess = false;
            this.showError = true;
            this.$notify.error({
              title: "Failed to edit info",
              showClose: true
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../assets/css/info.scss";
</style>



