<template>
  <div class="upload">
    <p class="title">Modify avatar</p>
    <hr />
    <div class="section">
      <el-upload
        drag
        :action="uploadUrl()"
        :show-file-list="false"
        :on-success="handleAvatarSuccess"
        :before-upload="beforeAvatarUpload"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">
          Drag the file to here,or<em>Modify avatar</em>
        </div>
        <div class="el-upload__tip" slot="tip">
          Only support upload JPG / PNG files and do not exceed 10M
        </div>
      </el-upload>
    </div>
  </div>
</template>

<script>
import mixin from "../mixins";
import { mapGetters } from "vuex";

export default {
  name: "upload",
  mixins: [mixin],
  data() {
    return {
      imageUrl: ""
    };
  },
  computed: {
    ...mapGetters(["userId"])
  },
  methods: {
    uploadUrl() {
      return `${this.$store.state.configure.HOST}/user/avatar/update?id=${this.userId}`;
    },
    handleAvatarSuccess(res, file) {
      if (res.code === 1) {
        this.imageUrl = URL.createObjectURL(file.raw);
        this.$store.commit("setAvator", res.avator);
        this.$message({
          message: "Edited successfully",
          type: "success"
        });
      } else {
        this.notify("Fail to modify", "error");
      }
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 10;
      if (!isJPG) {
        this.$message.error("Upload avatar pictures can only be jpg format!");
      }
      if (!isLt2M) {
        this.$message.error(
          "The size of uploaded avatar image cannot exceed 10MB!"
        );
      }
      return isJPG && isLt2M;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../assets/css/upload.scss";
</style>



